import React from 'react';
import s from './AlternativeAffairs.module.css';
import {FilterValueType} from './AlternativeAffairs';

type FilterButtonPropsType = {
    value: FilterValueType
    filter: FilterValueType
    setFilter: (value: FilterValueType) => void
}

export const FilterButton = (props: FilterButtonPropsType) => {
    const changeFilter = () => {
        props.setFilter(props.value)
    }

    return (
        <button
            onClick={changeFilter}
            className={`${s.btn} 
            ${props.filter===props.value ? s.active : ''}`}
        >
            {props.value}
        </button>
    )
}